function catalog() {
  // let height = 
  //å¤–å±‚å®¹å™¨
  let markdownBody = document.getElementsByClassName("markdown-body");
  //å†…å®¹åŒº
  let childrenList = markdownBody[0].children;
  //è¦åŒ¹é…çš„tagName
  let list = ["H1", "H2", "H3", "H4", "H5", "H6"];
  let objList = [];
  // console.log(childrenList);//innerText
  //éå†è·å–æ ‡ç­¾çš„é¡ºåº
  for (let i = 0; i < childrenList.length; i++) {
    if (list.indexOf(childrenList[i].tagName) >= 0) {
      objList.push({
        scrollTop: childrenList[i].offsetTop,
        id: childrenList[i].id,
        index: parseInt(childrenList[i].tagName.replace("H", "")),
        title: childrenList[i].innerText
      });
    }
  }
  // console.log(objList)
  //è½¬æ¢æˆæ ‘å½¢ç»“æ„
  let tree = treeify(objList, 0);

  function treeify(data, tag) {
    let tree = [];
    let index = 0;
    data.forEach(item => {
      item.children = [];
      let len = tree.length;
      if (len === 0) {
        item.tag =tag + (++index) + "."; // æ ‡é¢˜ç´¢å¼•ï¼ˆ1.ã€1.1.ç­‰ï¼‰
        tree.push(item); // ç¬¬ä¸€ä¸ªå…ƒç´ ï¼Œç›´æ¥æ”¾è¿›tree
      } else {
        let last = tree[len - 1];
        if (item.index <= last.index) {
          // å¦‚æœindexæ¯”treeæœ€åä¸€ä¸ªçš„indexå°æˆ–ç­‰äºï¼Œè¯´æ˜æ˜¯åŒçº§å­˜è¿›å»
          item.tag = tag + ++index + ".";
          tree.push(item);
        } else {
          last.children.push(item); // å¦åˆ™å­˜è¿›æœ€åä¸€ä¸ªå…ƒç´ çš„children
        }
      }
    });
    // å› ä¸ºä¸Šé¢ä¸€å±‚å¾ªç¯ï¼Œåªèƒ½å¤„ç†ä¸¤å±‚ï¼Œæ‰€ä»¥éœ€è¦éå†å­©å­èŠ‚ç‚¹ï¼Œå‡ºç°indexä¸ä¸€æ ·çš„è¯´æ˜ä¸æ˜¯åŒçº§ï¼Œéœ€è¦å¯¹å­©å­èŠ‚ç‚¹å†é€’å½’è°ƒç”¨ç”Ÿæˆ
    tree.forEach(item => {
      let children = item.children;
      let ids = [];
      index = 0;
      // åˆ¤æ–­æ˜¯å¦å­˜åœ¨indexä¸ä¸€æ ·çš„
      children.forEach(child => {
        child.tag = item.tag + ++index + ".";
        if (ids.indexOf(child.index) === -1) {
          ids.push(child.index);
        }
      });
      if (ids.length > 1) {
        // idsçš„å…ƒç´ å¤§äº1è¯´æ˜å­˜åœ¨ï¼Œéœ€è¦å†é€’å½’å­©å­èŠ‚ç‚¹
        item.children = treeify(children, item.tag);
      }
    });
    return tree;
  }
  // console.log(tree)
  return tree;
}
export default {
  catalog
};
