<template>
  <div class="tag-card-body">
    <TagCard v-for="(value,key) in tagList" :key="key" :tagList="tagList" :index="key" ref="list" />
    <div class="footer line"></div>
  </div>
</template>

<script>
//è¿™é‡Œå¯ä»¥å¯¼å…¥å…¶ä»–æ–‡ä»¶ï¼ˆæ¯”å¦‚ï¼šç»„ä»¶ï¼Œå·¥å…·jsï¼Œç¬¬ä¸‰æ–¹æ’ä»¶jsï¼Œjsonæ–‡ä»¶ï¼Œå›¾ç‰‡æ–‡ä»¶ç­‰ç­‰ï¼‰
//ä¾‹å¦‚ï¼šimport ã€Šç»„ä»¶åç§°ã€‹ from 'ã€Šç»„ä»¶è·¯å¾„ã€‹';
import TagCard from "../components/TagCard";
export default {
  //importå¼•å…¥çš„ç»„ä»¶éœ€è¦æ³¨å…¥åˆ°å¯¹è±¡ä¸­æ‰èƒ½ä½¿ç”¨
  components: {
    TagCard
  },
  data() {
    //è¿™é‡Œå­˜æ”¾æ•°æ®
    return {
      tagList: [
        {
          name: "",
          num: Number,
          _id: ""
        }
      ]
    };
  },
  //ç›‘å¬å±æ€§ ç±»ä¼¼äºdataæ¦‚å¿µ
  computed: {},
  //ç›‘æ§dataä¸­çš„æ•°æ®å˜åŒ–
  watch: {},
  //æ–¹æ³•é›†åˆ
  methods: {
    tagSort() {
      console.log("tagSort");
      this.tagList = this.tagList.sort((l, r) => r.num - l.num);
    },
    colorful() {
      let ele = this.$refs.list;
      console.log(ele[0]);
      for (let i = 0; i < ele.length; i++) {
        if (i < 1) {
          ele[i].$el.style.background = "rgba(255, 134, 50, .8)";
        }
        if (i >= 1) {
          ele[i].$el.style.background = "rgba(47, 147, 180, 1)";
        }
        if (i >= 2) {
          ele[i].$el.style.background = "rgba(47, 147, 180, 0.7)";
        }
        if (i > 3) {
          ele[i].$el.style.background = "rgba(187, 187, 238, 0.9)";
        }
      }
      //   ele[0].style.background = 'red'
      //   console.log(ele[0].$el.style);
    },
    async fetchTags() {
      const res = await this.$http.get("tags/list");
      this.tagList = res.data;
      this.tagSort();
    }
  },
  //ç”Ÿå‘½å‘¨æœŸ - åˆ›å»ºå®Œæˆï¼ˆå¯ä»¥è®¿é—®å½“å‰thiså®ä¾‹ï¼‰
  created() {
    this.fetchTags();
  },
  //ç”Ÿå‘½å‘¨æœŸ - æŒ‚è½½å®Œæˆï¼ˆå¯ä»¥è®¿é—®DOMå…ƒç´ ï¼‰
  mounted() {},
  beforeCreate() {}, //ç”Ÿå‘½å‘¨æœŸ - åˆ›å»ºä¹‹å‰
  beforeMount() {}, //ç”Ÿå‘½å‘¨æœŸ - æŒ‚è½½ä¹‹å‰
  beforeUpdate() {}, //ç”Ÿå‘½å‘¨æœŸ - æ›´æ–°ä¹‹å‰
  updated() {
    this.colorful();
  }, //ç”Ÿå‘½å‘¨æœŸ - æ›´æ–°ä¹‹å
  beforeDestroy() {}, //ç”Ÿå‘½å‘¨æœŸ - é”€æ¯ä¹‹å‰
  destroyed() {}, //ç”Ÿå‘½å‘¨æœŸ - é”€æ¯å®Œæˆ
  activated() {} //å¦‚æœé¡µé¢æœ‰keep-aliveç¼“å­˜åŠŸèƒ½ï¼Œè¿™ä¸ªå‡½æ•°ä¼šè§¦å‘
};
</script>
<style  scoped>
