  width: 98%;
  margin: 2rem 1%;
  /* background: blue; */
  padding: 3%;
  box-shadow: 0.5rem 0.5rem 1.8rem #888;
}
.tag-card-body /deep/ .tag-body {
  color: white;
  border: none;
  font-weight: bold;
}

.tag-card-body /deep/ .tag-body:hover {
